import React, { useState } from 'react'
import { useFormik } from 'formik'
import * as yup from 'yup'
import axios from 'axios'
import { useNavigate } from 'react-router-dom'

export default function ForgitPassword() {
  let [loading ,setLoading] = useState(false)
const [flage,setFlag] = useState(false)
const [Err,setErr]=useState('')
let navigate= useNavigate()

async function fogotPassword(values){
  setLoading(true)
let {data} = await axios.post('https://route-ecommerce.onrender.com/api/v1/auth/forgotPasswords',values)

console.log(data);

 if(data.statusMsg === "success"){
   setFlag(true)
  setLoading(false)

  // navigate('/login')
 }

}

let myValidation =yup.object({
  email : yup.string().required('email is required').email(),

})


  let formik =useFormik({
  initialValues:{
    email:'',
   
    
  },
  
  // validate ,
  validationSchema:myValidation ,
  onSubmit:(values)=>{
    fogotPassword(values);
    console.log(values);
  }
})


async function resetPassword(values){
  setLoading(true)
let {data} = await axios.post('https://route-ecommerce.onrender.com/api/v1/auth/verifyResetCode',values).catch((err)=>{
setErr(err.response.data.message);
   setLoading(false)

})

console.log(data);

 if(data.status === "Success"){
   setLoading(false)
  
  navigate('/reset-password')
 }

}
  let formik1 =useFormik({
  initialValues:{
    resetCode:'',
   
    
  },
  

  onSubmit:(values)=>{
    resetPassword(values);
    console.log(values);
  }
})
  return (<>
    {flage ? 
        <div className='w-75 m-auto py-5'>
            <h1 className='text-center my-2 text-decoration-underline'>Reset Password </h1>
            <form onSubmit={formik1.handleSubmit} >
          
            <label htmlFor="resetCode"> resetCode :</label>
            <input type="text" onBlur={formik1.handleBlur} onChange={formik1.handleChange} value={formik1.values.resetCode} className="form-control my-2" name="resetCode" id="resetCode"/>
            {formik1.errors.resetCode && formik1.touched.resetCode  ?  <div className='alert fw-bolder alert-danger text-center'> {formik1.errors.resetCode}</div> : null }
            {Err !=='' ? <div className='alert alert-danger ' > {Err}</div> : null}
            {loading ?   <button className='text-white btn my-3 bg-main'><i className='fas fa-spinner fa-spin'></i></button>:
            <button disabled={!(formik1.isValid && formik1.dirty)} className='text-white btn my-3 bg-main' type='submit'>Veryfiy Code</button>
                      }
          </form>
          
        </div>
  :

        <div className='w-75 m-auto py-5'>
            <h1 className='text-center my-2'>Forget Password </h1>
            <form onSubmit={formik.handleSubmit} >
          
            <label htmlFor="email"> email :</label>
            <input type="email" onBlur={formik.handleBlur} onChange={formik.handleChange} value={formik.values.email} className="form-control my-2" email="email" id="email"/>
            {formik.errors.email && formik.touched.email  ?  <div className='alert fw-bolder alert-danger text-center'> {formik.errors.email}</div> : null }

            {loading ?   <button className='text-white btn my-3 bg-main'><i className='fas fa-spinner fa-spin'></i></button>:
            <button disabled={!(formik.isValid && formik.dirty)} className='text-white btn my-3 bg-main' type='submit'>Send</button>
                      }
          </form>
          
        
        
        </div>

  }
    

    
    </>
    )
}
