import React, { useState } from 'react'
import { useFormik } from 'formik'
import * as yup from 'yup'
import axios from 'axios'
import { useNavigate } from 'react-router-dom'
// import { Link } from 'react-router-dom'

export default function ResetPassword() {
  let [loading ,setLoading] = useState(false)
let [errorm ,setErr] = useState('')
let navigate= useNavigate()
let baseUrl ='https://route-ecommerce.onrender.com'
async function sendData(values){
  setLoading(true)
let {data} = await axios.put(`${baseUrl}/api/v1/auth/resetPassword`,values).catch((err)=>{
setLoading(false)
setErr(err.response.data.message)
})

console.log(data);

if(data.token){
  setLoading(false)
  navigate('/login')
}

}

let myValidation =yup.object({
  email : yup.string().required('email is required').email(),
  newPassword : yup.string().required('newPassword is Required')
})


  let formik =useFormik({
  initialValues:{
    email:'',
    newPassword:''
    
  },
  // validate ,
  validationSchema:myValidation ,
  onSubmit:(values)=>{
    sendData(values);
  }
})

  return (
    <div className='w-75 m-auto py-5'>
      <h1 className='text-center my-5'>Reset New Passwore </h1>
      <form onSubmit={formik.handleSubmit} >
     {errorm.length >0 ? <div className='alert alert-danger text-capitalize'>{errorm}</div>:null}
      
        <label htmlFor="email"> email :</label>
        <input type="email" onBlur={formik.handleBlur} onChange={formik.handleChange} value={formik.values.email} className="form-control my-2" email="email" id="email"/>
        {formik.errors.email && formik.touched.email  ?  <div className='alert fw-bolder alert-danger text-center'> {formik.errors.email}</div> : null }

        <label htmlFor="newPassword"> newPassword :</label>
        <input type="password" onBlur={formik.handleBlur} onChange={formik.handleChange} value={formik.values.newPassword} className="form-control my-2" name="newPassword" id="newPassword"/>
        {formik.errors.newPassword && formik.touched.newPassword ?  <div className='alert alert-danger text-center fw-bolder'> {formik.errors.newPassword}</div> : null }    

      {loading ?   <button className='text-white btn my-3 bg-main'><i className='fas fa-spinner fa-spin'></i></button>:
         <button disabled={!(formik.isValid && formik.dirty)} className='text-white btn my-3 bg-main' type='submit'>Submite</button>
                   }
      </form>
      
    </div>)
}
