import React from 'react'
import { Link } from 'react-router-dom'
import logo from '../../assets/imgs/freshcart-logo.svg'

export default function Nav({userdata ,logOut}) {
  return (
    <div>
      <nav className="navbar navbar-expand-lg bg-body-tertiary">
  <div className="container-fluid">
    <Link className="navbar-brand" to="\"><img src={logo} alt='' className='w-100'/></Link>
    
    <div className="collapse navbar-collapse" id="navbarSupportedContent">
    
    
     {userdata !== null ?  <ul className="navbar-nav me-auto mb-2 mb-lg-0">
        <li className="nav-item">
          <Link className="nav-link " to="home">Home</Link>
        </li>
        
        
       <li className="nav-item">
          <Link className="nav-link" to="products">Products</Link>
        </li>
        
        
      <li className="nav-item">
          <Link className="nav-link" to="cart"><i className="fa fs-3 text-main fa-sharp  fa-cart-shopping"></i></Link>
      
        </li>


      </ul>
      : null }
     
  
    <ul className="navbar-nav ms-auto mb-2 mb-lg-0 d-flex align-items-center">
        <li className="nav-item">
         <Link target='_blank' to='https://www.facebook.com/M7MAD.GABER11?mibextid=ZbWKwL'><i className='fab mx-2 fa-facebook fa-beat'></i></Link> 
         <Link target='_blank' to='https://www.instagram.com/m7amed_gaber23/?igshid=ZDdkNTZiNTM%3D'><i className='fab mx-2 fa-instagram fa-beat'></i></Link> 
         <Link target='_blank' to='https://www.linkedin.com/in/mohamed-gaber-52521b20a/'><i className='fab mx-2 fa-linkedin fa-beat'></i></Link> 
         <Link target='_blank' to=''><i className='fab mx-2 fa-twitter fa-beat'></i></Link> 
         <Link target='_blank' to='https://github.com/settings/profile'><i className='fab mx-2 fa-git fa-beat'></i></Link> 
          
          
          
          

        </li>

{userdata !==null ? 

         <li className="nav-item">
          <Link className="nav-link" onClick={logOut} >Log Out</Link>
        </li>
:
          <>
          <li className="nav-item">
                    <Link className="nav-link " to="register">Register</Link>
                  </li>
                  <li className="nav-item">
                    <Link className="nav-link" to="login" >Log in</Link>
                  </li>
          </> }
        
        

       
        
      </ul>

      
    </div>
  </div>
</nav>
      
    </div>
  )
}
