import React, { useState } from 'react'
import { useFormik } from 'formik'
import * as yup from 'yup'
import axios from 'axios'
import { useNavigate } from 'react-router-dom'


export default function Register() {
let [loading ,setLoading] = useState(false)
let [errorm ,setErr] = useState('')
let navigate= useNavigate()



async function sendData(values){
  setLoading(true)
let {data} = await axios.post('https://route-ecommerce.onrender.com/api/v1/auth/signup',values).catch((err)=>{
setLoading(false)
setErr(err.response.data.errors.msg)
})

console.log(data);

if(data.message === "success"){
  setLoading(false)
  navigate('/login')
}

}

let myValidation =yup.object({
  name : yup.string().required('Name is Required').min(3,'min length is 3'),
  email : yup.string().required('email is required').email(),
  phone : yup.string().required('phone is Required').matches(/^01[0125][0-9]{8}$/i,'phone is not valied'),
  password : yup.string().required('password is Required').matches(/^[A-Z][a-z0-9]{6,}$/i,'password is not valied'),
  rePassword : yup.string().required('rePassword is Required').oneOf([yup.ref('password')],'not valied rePassword')
})


  let formik =useFormik({
  initialValues:{
    name:'',
    email:'',
    password:'',
    rePassword:'',
    phone:''
  },
  // validate ,
  validationSchema:myValidation ,
  onSubmit:(values)=>{
    sendData(values);
    console.log(errorm);
  }
})

  return (
    <div className='w-75 m-auto py-5'>
      <h1 className='text-center my-2'>Regsteration Form </h1>
      <form onSubmit={formik.handleSubmit} >
     {errorm.length >0 ? <div className='alert alert-danger text-capitalize'>{errorm}</div>:null}
        <label htmlFor="name"> name :</label>
        <input type="text" onBlur={formik.handleBlur} onChange={formik.handleChange} value={formik.values.name} className="form-control my-2" name="name" id="name"/>
         {formik.errors.name && formik.touched.name ?  <div className='alert alert-danger text-center fw-bolder'> {formik.errors.name}</div> : null }

        <label htmlFor="email"> email :</label>
        <input type="email" onBlur={formik.handleBlur} onChange={formik.handleChange} value={formik.values.email} className="form-control my-2" email="email" id="email"/>
        {formik.errors.email && formik.touched.email  ?  <div className='alert fw-bolder alert-danger text-center'> {formik.errors.email}</div> : null }

        <label htmlFor="password"> password :</label>
        <input type="password" onBlur={formik.handleBlur} onChange={formik.handleChange} value={formik.values.password} className="form-control my-2" name="password" id="password"/>
        {formik.errors.password && formik.touched.password ?  <div className='alert alert-danger text-center fw-bolder'> {formik.errors.password}</div> : null }    

        <label htmlFor="rePassword"> rePassword :</label>
        <input type="password" onBlur={formik.handleBlur} onChange={formik.handleChange} value={formik.values.rePassword} className="form-control my-2" name="rePassword" id="rePassword"/>
         {formik.errors.rePassword  && formik.touched.rePassword ?  <div className='alert alert-danger text-center fw-bolder'> {formik.errors.rePassword}</div> : null }

       <label htmlFor="phone"> Phone :</label>
        <input type="phone" onBlur={formik.handleBlur} onChange={formik.handleChange} value={formik.values.phone} className="form-control my-2" name="phone" id="phone"/>
       {formik.errors.phone && formik.touched.phone ?  <div className='alert alert-danger text-center fw-bolder'> {formik.errors.phone}</div> : null }

      {loading ?    <button className='text-white btn my-3 bg-main'><i className='fas fa-spinner fa-spin'></i></button>:
         <button disabled={!(formik.isValid && formik.dirty)} className='text-white btn my-3 bg-main' type='submit'>Submite</button>
                   }
      </form>
      
    </div>
  )
}
