import axios from 'axios'
import React, { useContext, useEffect, useState } from 'react'
import { toast } from 'react-hot-toast';
import { useParams } from 'react-router-dom'
import { cartContext } from '../../Context/CartContext';

export default function ProudectDet() {
let{id}=useParams()
let{addTo}=useContext(cartContext)

async function addProd(id){
  let {data} = await addTo(id)

  if(data.status === "success"){
    toast.success(data.message ,{duration:2000});
  }else{
    toast.error('err');
  }

}
  

  const [data,setData] = useState({})

  
async  function getById(){
    let{data}=await axios.get(`https://route-ecommerce.onrender.com/api/v1/products/${id}`)
setData(data.data)
  }
useEffect(()=>{
    getById(id);

  })

  return ( <div className="container my-5">
     <div className="row d-flex align-items-center ">
            <div className="col-md-4">
              <img src={data.imageCover} className='w-100' alt="" />
            </div>

            <div className="col-md-8">
              <p>{data.title}</p>
              <p className='my-3 text-muted'>{data.description}</p>
              {/* <p><span className='text-main'>Category:</span> {data.category}</p> 
              <p><span className='text-main '>Brand :</span> {data.brand}</p>
            */}
            
            <div className=" d-flex justify-content-between my-3">
            <span> {data.price} EGP</span>
            <span> {data.ratingsAverage}<i className='fa-solid rating-color fa-star'></i></span>
         
              </div> 

          <button className='my-4 w-100 btn bg-main text-white' onClick={()=>{ addProd(data._id) }}> + Add To Cart </button>
            </div>

     </div>
    </div>
  )
}
