import axios from "axios";
import { createContext } from "react";

export let cartContext = createContext()



export default function CartContextProvider(props){

let uToken = localStorage.getItem('userToken')
let headers = {Token:uToken}
    function addTo(id){
        return axios.post('https://route-ecommerce.onrender.com/api/v1/cart',{productId:id},{headers}).then((response)=>response).catch((err)=>err)
      
      
    }
    function getLoggedCart() {
        return axios.get('https://route-ecommerce.onrender.com/api/v1/cart',{ headers }).then((response) => response).catch((err) => err)


    }
    function updatCart(id,count) {
        return axios.put(`https://route-ecommerce.onrender.com/api/v1/cart/${id}`, { count:count }, { headers }).then((response) => response).catch((err) => err)


    }
    function deleteCart(id) {
        return axios.delete(`https://route-ecommerce.onrender.com/api/v1/cart/${id}`, { headers }).then((response) => response).catch((err) => err)


    }
    function clearCart(id) {
        return axios.delete(`https://route-ecommerce.onrender.com/api/v1/cart`, { headers }).then((response) => response).catch((err) => err)


    }
    return(
        <cartContext.Provider value={{ addTo, getLoggedCart, updatCart, deleteCart ,clearCart} }>
        {props.children}
        </cartContext.Provider>
    )
}