import axios from 'axios'
import React, { useContext, useEffect, useState } from 'react'
import { Link } from 'react-router-dom'
import { cartContext } from '../../Context/CartContext'
import  toast  from 'react-hot-toast'
export default function FutearComponent() {

let{addTo}=useContext(cartContext)

async function addProd(id){

  let {data} = await addTo(id)

  if(data.status === "success"){
    toast.success(data.message ,{duration:2000});
  }else{
    toast.error('err');
  }

}


  let[Proudect ,setProudect]=useState([])

 async function getProd(){
    let{data} =await axios.get('https://route-ecommerce.onrender.com/api/v1/products')
    setProudect(data.data)
    
  }

  useEffect(()=>{
    getProd()
  })
 
  return (
    <div className="row gy-5">
      {Proudect.slice(0,12).map((prod,index)=>{
        return  <div className="col-md-3 " key={index}>
          <Link to={`/products/${prod._id}`}>
          <img src={prod.imageCover} className='w-100' alt="" />
          <h5 className='text-main'>{prod.title.split(" ").slice(0,2).join(" ")}....</h5>
          <div className="box d-flex justify-content-between">
            <span> {prod.ratingsAverage}<i className='fa-solid rating-color fa-star'></i></span>
            <span> {prod.price} EGP</span>
          </div> 
           </Link>
      <button className='btn bg-main text-white w-100 btn-sm' onClick={ ()=>{ addProd(prod._id) } }>+ Add </button>
        </div>
       
        
       

      

      })}
    </div>
  )
}
