import React, { useContext, useEffect, useState } from 'react'
import { cartContext } from '../../Context/CartContext'
import { Helmet } from 'react-helmet'

export default function Cart() {
 
let[CartD,setCartD] = useState(null)
// clearCart
 let {getLoggedCart ,updatCart,deleteCart}= useContext(cartContext)
 
 async function getData(){
  let res = await getLoggedCart()
  // console.log(data.data);
  setCartD(res.data.data);

 }

async function updata(id,count){
  let res = await updatCart(id,count)
  setCartD(res.data.data);

}
async function deleteSomeCart(id){
  let res = await deleteCart(id)
  setCartD(res.data.data);

}
//////
// async function deleteAllCart(){
//   let res = await clearCart()
//   setCartD(res.data.data);
//   CartD.totalCartPrice=0
// }
 useEffect(()=>{
  getData()
 })

  return (<> 
  
      <Helmet>
        <meta charSet="utf-8" />
        <title>Cart</title>
      </Helmet>


  <div className='container'>
      <div className=" p-4 bg-main-light">
      {CartD !== null ? 
      <div className="item">
        <h2 className='fw-bold text-main'>Shope Cart</h2> 
      {/* ////// */}
        {/* <button onClick={deleteAllCart} className='w-100 btn btn-outline-dark'> Clear</button> */}
      {CartD.totalCartPrice > 0 ? <h4> total price:{CartD.totalCartPrice} EGP</h4> : null} 

     {CartD.products.filter((product)=> product.count > 0 ).map((prod,index)=>{
      return  <div className="row p-5 my-5" key={index}>
       
         <div className='col-md-1'>
        <img src={prod.product.imageCover} className='w-100' alt="" />
        
        </div>
        <div className="col-md-11">
          <div className="box d-flex justify-content-between">
             <div>
                <h6>{prod.product.title}</h6>
              <h6>{prod.price} EGP</h6>
              <button onClick={()=>{deleteSomeCart(prod.product._id)}} className='btn btn-outline-danger '><i className='fa-solid fa-trash mx-3'></i> remove </button>
        
             </div>
              <div>
              <button className='btn m-4 btn-outline-success ' onClick={()=>{updata(prod.product._id,++prod.count)}}>+</button>
             <span className='m-4'>{prod.count}</span>
              <button className='btn m-4 btn-outline-success ' onClick={()=>{updata(prod.product._id,prod.count-1)}}>-</button>


                  </div>
          </div>
      
        </div>
        
        
       
      
            </div>
      })}
  
     
      </div>
    
      :null
      }

      </div>
      
      {}
    </div>
  
  </>
    
    
  )
}
