import React, { useContext, useEffect, useState } from 'react'
import { useDispatch } from 'react-redux'
// import {increase,decrease} from '../../Redux/CounterSlice'
import { getProducts } from '../../Redux/ProudectSlice'
import { toast } from 'react-hot-toast';
import { cartContext } from '../../Context/CartContext';
import { Helmet } from 'react-helmet';


export default function Products() {


let{addTo}=useContext(cartContext)

async function addProd(id){
  let {data} = await addTo(id)

  if(data.status === "success"){
    toast.success(data.message ,{duration:2000});
  }else{
    toast.error('err');
  }

}

 const[pr , setProducts] =useState()

 async function getprod(){
  let x = await dispatch(getProducts())
   setProducts(x.payload);

  // console.log(x.payload);
 }
useEffect(()=>{
  getprod()
})

// let{counter} = useSelector((state)=>state.counter)
 let dispatch = useDispatch()


return (
  <>
           <Helmet>
                <meta charSet="utf-8" />
                <title>Products</title>
            </Helmet>
            
{typeof(pr) === 'undefined' ? 
            <div className='text-main  bg-layer'><i className='fas fw-bolder fs-1 fa-spinner fa-spin'></i></div>  :

                 <div className='container mt-5'>
        <div className="row">
            <div className="col-md-12">
            <h2 className='text-main fst-italic'> All Products </h2>
            </div>
            
    {pr.map((produ,index)=>{
              return <div className="col-md-3" key={index} >
                <img src={produ.imageCover} className='w-100' alt="" />
              <p>{produ.title}</p>
              <div className="box d-flex justify-content-between">
            <span> {produ.ratingsAverage}<i className='fa-solid rating-color fa-star'></i></span>
            <span> {produ.price} EGP</span>
          </div> 
              <button className='my-4 w-100 btn bg-main text-white' onClick={()=>{ addProd(produ.id) }}> + Add To Cart </button> 

                 </div> 

              })}
              

      </div>
    </div>


}


     
           
          
    {/* {pr.map((produ,index)=>{
              return <div className="col-md-3" key={index} >
                <img src={produ.imageCover} className='w-100' alt="" />
              <p>{produ.title}</p>
              <button className='my-4 w-100 btn bg-main text-white' onClick={()=>{ addProd(produ.id) }}> + Add To Cart </button> 

                 </div> 

              })} */}
  
  </>
  
  
  
  )
}
