import FutearComponent from '../FutearComponent/FutearComponent';
import { Helmet } from 'react-helmet';

export default function Home() {
  return (
    <div>
       <Helmet>
                <meta charSet="utf-8" />
                <title>Home </title>
            </Helmet>
      
       <Header/>
  
      
      
      <div className="container">
        
          <div className="row gy-5">
             <h2 className='pb-5 '>Populer Categoreis</h2>
             <Item/>

           <div className="col-md-12"><h2 className='pb-5 my-5 '>Populer Proudect</h2>            
              </div>  
             <FutearComponent/>
          </div>
            

      </div>


    </div>
  )
}
