import { RouterProvider, createBrowserRouter, createHashRouter } from 'react-router-dom';

import './App.scss';

import Layout from './components/Layout/Layout'
import Products from './components/Products/Products'
import LogIn from './components/LogIn/LogIn'
import Cart from './components/Cart/Cart'
import NotFound from './components/NotFound/NotFound'
import Home from './components/Home/Home';
import ResetPassword from './components/ResetPassword/ResetPassword';
import ForgitPassword from './components/ForgitPassword/ForgitPassword';
import Register from './components/Register/Register';
import { useEffect, useState } from 'react';
import jwtDecode from 'jwt-decode';
import ProtectedRoute from './components/protectedRoute/protectedRoute';
import ProudectDet from './components/ProudectDet/ProudectDet';
import CartContextProvider from './Context/CartContext';
import { Provider } from 'react-redux';
import { Store } from './Redux/Store';





function App() {
const [userdata,setuserData]= useState(null)

function savatUserData(){
  let incodedToken= localStorage.getItem('userToken')
  let decodedToken = jwtDecode(incodedToken)
 setuserData(decodedToken);
}


useEffect(()=>{
if(localStorage.getItem('userToken') !== null ){
  setuserData()
}

},[])
  const router= createHashRouter([
  {path:'' , element :<Layout setuserData={setuserData} userdata={userdata}/>,children:[
    {path:'',element:<ProtectedRoute><Home/></ProtectedRoute> },
    {path:'home' ,element:<ProtectedRoute><Home/></ProtectedRoute>},
    {path:'products' ,element: <ProtectedRoute><Products/></ProtectedRoute>},
    {path:'products/:id' ,element: <ProtectedRoute><ProudectDet/></ProtectedRoute>},
    {path:'cart' ,element:<ProtectedRoute><Cart/></ProtectedRoute>},
    {path:'login' ,element:<LogIn savatUserData={savatUserData}/>},
    {path:'reset-password' ,element:<ResetPassword/>},
    {path:'forgit-Password' ,element:<ForgitPassword/>},
    {path:'register' ,element:<Register/>},
    {path:'protectedRoute' ,element:<ProtectedRoute/>},
    {path: '*' ,element:<NotFound/>}
   
  ]}
])
  return (
<Provider store={Store}>
  <CartContextProvider>
        <RouterProvider router={router}></RouterProvider> 
  </CartContextProvider>
</Provider>
 
  );
}

export default App;
